<template>
  <nav id="nav" class="navbar" role="navigation" aria-label="main navigation">
    <div class="navbar-brand">
      <a class="navbar-item" >
        <router-link to="/">
          <img src="@/assets/images/medkit.png" alt="Medkit">
        </router-link>
      </a>
      <a role="button" class="navbar-burger" aria-label="menu" aria-expanded="false" data-target="mainNavbar">
        <span aria-hidden="true"></span>
        <span aria-hidden="true"></span>
        <span aria-hidden="true"></span>
      </a>
    </div>
    <div id="mainNavbar" class="navbar-menu">
      <div class="navbar-start">
          <router-link class="navbar-item" to="/">
            Home
          </router-link>
          <router-link v-show="isJui" class="navbar-item" to="/drug">
            Drug
          </router-link>
          <router-link class="navbar-item" to="/nutrition">
            Nutrition
          </router-link>
          <router-link class="navbar-item" to="/drugdose">
            Drug Dose
          </router-link>
      </div>
      <div class="navbar-end">
        <div class="navbar-item">
          <div class="buttons">
          </div>
        </div>
      </div>
    </div>
  </nav>
</template>

<script>
  export default {
    name: "Navbar",
    mounted: function(){
      const $navbarBurgers = Array.prototype.slice.call(document.querySelectorAll('.navbar-burger'), 0);
      if ($navbarBurgers.length > 0) {
        $navbarBurgers.forEach( el => {
          el.addEventListener('click', () => {
            const target = el.dataset.target;
            const $target = document.getElementById(target);

            el.classList.toggle('is-active');
            $target.classList.toggle('is-active');

          });
        });
      }
    },
    computed: {
      isJui () {
        return true
        // return !!(this.$route.query.jui)
      }
    }
  }
</script>

<style scoped>
.navbar-item img {
  max-height: 45px;
}

</style>
