import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('@/views/Home')
  },
  {
    path: '/drug',
    name: 'Drug',
    component: () => import('@/views/Drug')
  },
  {
    path: '/nutrition',
    name: 'Nutrition',
    component: () => import('@/views/Nutrition')
  },
  {
    path: '/drugdose',
    name: 'DrugDose',
    component: () => import('@/views/DrugDose')
  },
]

const router = new VueRouter({
  routes
})

export default router
